.list {
  font-size: 14px;
  font-weight: normal;
  line-height: 26px;
  text-align: justify;
  text-justify: inter-word;
  margin: 30px 0px;
}

.list li{
list-style: none;
}
.list li:first-child {
  font-family: 'Saira', sans-serif;
  text-transform: uppercase;
  font-size: 14px;
  color: #E9204F;
  font-weight: bold;
}

.list li:nth-child(2) {
